import * as styles from "../styles/restaurant.module.css";
import { useSelector } from "react-redux";
import React from "react";
import { Link } from "gatsby";
import SocialMediaIcon from "./icons";

const SUPPORTED_PLATFORMS = ["maps", "facebook", "twitter", "instagram"];

// Utility function to map URLs to platform names
const getPlatformFromUrl = (url) => {
    if (url.includes("maps.app.goo.gl") || url.includes("google.com/maps")) {
        return "maps";
    }
    if (url.includes("facebook.com")) {
        return "facebook";
    }
    if (url.includes("twitter.com") || url.includes("x.com")) {
        return "twitter";
    }
    if (url.includes("instagram.com")) {
        return "instagram";
    }
    return null; // Return null if the URL doesn't match any supported platform
};

// Check if the link is internal
const isInternalLink = (url) => /^\/(?!\/)/.test(url);

const SocialMedia = () => {
    const { spreadsheet } = useSelector((state) => state.sitesDemo);

    const links = [
        { name: "map url", link: spreadsheet["map url"] },
        { name: "social media", link: spreadsheet["social media"] },
    ];

    return (
        <div>
            {links
                .map(({ link }) => {
                    const platform = getPlatformFromUrl(link);
                    return platform && SUPPORTED_PLATFORMS.includes(platform)
                        ? { platform, link }
                        : null;
                })
                .filter(Boolean) // Remove null values
                .map(({ platform, link }, index) =>
                    isInternalLink(link) ? (
                        <Link
                            key={index}
                            className={styles.restaurant_home_item}
                            to={link}
                        >
                            <SocialMediaIcon app={platform} />
                        </Link>
                    ) : (
                        <a
                            key={index}
                            className={styles.restaurant_home_item}
                            href={link}
                            target="_blank"
                            rel="noopener noreferrer"
                        >
                            <SocialMediaIcon app={platform} />
                        </a>
                    )
                )}
        </div>
    );
};

export default SocialMedia;
