import React from "react";
import { updateCell } from "../../state/sitesDemoSlice";
import { useDispatch, useSelector } from "react-redux";
import * as styles from "../../styles/sitesDemo.module.css";

const SpreadsheetRenderer = () => {
    const spreadsheet = useSelector((state) => state.sitesDemo.spreadsheet);
    const dispatch = useDispatch();

    const handleCellChange = (key, value) => {
        dispatch(updateCell({ key, value })); // Dispatch the updated key and value
    };

    return (
        <div className={styles.spreadsheet}>
            <table>
                <thead>
                    <tr>
                        <th>DEMO</th>
                        <th>EDIT BELOW</th>
                    </tr>
                </thead>
                <tbody>
                    {Object.entries(spreadsheet).map(([key, value], index) => (
                        <tr key={index}>
                            <td className={styles.uneditableCell}>{key}</td>
                            <td>
                                <input
                                    type="text"
                                    value={value}
                                    onChange={(e) => handleCellChange(key, e.target.value)} // Update Redux state on input
                                    className={styles.editableInput}
                                />
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

export default SpreadsheetRenderer;
