import React from "react";
import * as styles from "../../styles/sitesDemo.module.css";

const WaveText = ({ text }) => {
    const colors = ["#FF0000", "#FF7F00", "#FFFF00", "#00FF00", "#0000FF", "#4B0082", "#8B00FF"]; // Rainbow colors

    return (
        <div className={styles.textWrapper}>
            <svg xmlns="http://www.w3.org/2000/svg" className={styles.svgText}>
                <text
                    x="50%"
                    y="50%"
                    textAnchor="middle"
                    dominantBaseline="middle"
                    fontSize="24"
                    fontFamily="Arial, sans-serif"
                    className="wave-text"
                >
                    {text.split("").map((char, index) => (
                        <tspan
                            key={index}
                            dx={`${index === 0 ? 0 : 6}`}
                            fill={colors[index % colors.length]} // Cycle through rainbow colors
                            style={{
                                animation: `wave 1.5s infinite`,
                                animationDelay: `${index * 0.1}s`,
                            }}
                        >
                            {char}
                        </tspan>
                    ))}
                </text>
                <style>
                    {`
                    @keyframes wave {
                        0%, 100% {
                            transform: translateY(0);
                            opacity: 1;
                        }
                        50% {
                            transform: translateY(-10px);
                            opacity: 0.5;
                        }
                    }

                    .wave-text tspan {
                        display: inline-block;
                    }
                    `}
                </style>
            </svg>
        </div>
    );
};

export default WaveText;
