import React from "react";
import { useSelector } from "react-redux";
import * as styles from "../../styles/sitesDemo.module.css";
import * as glowStyles from "../../styles/glow.module.css";
import WaveText from "./waveText";
import FlashingText from "./flashingText";
import SocialMedia from "../socialMedia";

const SpreadsheetRenderer = () => {
    const spreadsheet = useSelector((state) => state.sitesDemo.spreadsheet);

    return (
        <div className={[styles.renderer, glowStyles.glow].join(" ")}>
            <WaveText text={spreadsheet.name} />
            <FlashingText text={spreadsheet.about} />
            <WaveText text={spreadsheet.contact} />
            <SocialMedia />
        </div>
    );
};

export default SpreadsheetRenderer;
